import "./App.css";
import Timer from "./Timer.js";
import React, { useState } from "react";

function App() {
  const [Row, setRow] = useState(0);
  let Row1 = [];
  const [Column, setColumn] = useState(0);
  let Column1 = [];
  for (let i = 0; i < Row; i++) {
    Row1.push(1);
  }
  for (let i = 0; i < Column; i++) {
    Column1.push(1);
  }
  return (
    <>
      <form>
        <label>
          Number of Rows: &nbsp;
          <input
            type="number"
            value={Row}
            onChange={(e) => setRow(e.target.value)}
            name="row"
          />
        </label>
        <br />
        <br />
        <label>
          Number of Cols: &nbsp;
          <input
            type="number"
            value={Column}
            onChange={(e) => setColumn(e.target.value)}
            name="col"
          />
        </label>
        <br />
        <br />
      </form>
      <div class="timer">
        <Timer />
      </div>

      <div className="App">
        <table>
          <span> </span>
          <th>
            {Column1.map((item) => {
              return (
                <input
                  type="text"
                  name="row"
                  style={{ width: 110, marginInline: 10 }}
                />
              );
            })}
          </th>

          <tr>
            {Row1.map((item) => {
              return (
                <tr>
                  <div style={{ marginBlock: 10 }}>
                    <input type="text" name="row" />
                    <br />
                    <select>
                      <option value="easy">Easy</option>
                      <option value="medium">Medium</option>
                      <option selected value="Hard">
                        Hard
                      </option>
                    </select>
                  </div>
                </tr>
              );
            })}

            <td>
              {Row1.map((item) => {
                return (
                  <div className="Columns" style={{ display: "flex" }}>
                    {Column1.map((item) => {
                      return (
                        <select style={{ width: 100, margin: 20 }}>
                          <option value="1">O(1)</option>
                          <option value="log">O(log N)</option>
                          <option selected value="n">
                            O
                          </option>
                          <option value="nlog">O(N log N)</option>
                          <option selected value="n2">
                            O(n^2)
                          </option>{" "}
                          &nbsp;
                        </select>
                      );
                    })}
                  </div>
                );
              })}
            </td>
          </tr>
        </table>
      </div>
      <div class="buttonHolder">
        <input type="submit" value="Submit" />
      </div>
    </>
  );
}

export default App;
